.iframewithedgeAnimate-inner {

  .iframewithedgeAnimate-iframe-holder {
    width: 100%;
    height: 100%;
    text-align: center;
    overflow: hidden;
  }

  .iframewithedgeAnimate-iframe {
    border: none;
    width: 100%;
    height: 100%;
    vertical-align: top;

    &.scale {
      transform-origin: 0 0%;
      -ms-transform-origin: 0 0%;
      -moz-transform-origin: 0 0%;
      -o-transform-origin: 0 0%;
      -webkit-transform-origin: 0 0%;
    }
  }

  .iframewithedgeAnimate-controllers {
    background-color: #DCDDDE;
    border-radius: 5px;

    .iframewithedgeAnimate-playpause {
      float: left;
      width: 30px;
      height: 34px;
      cursor: pointer;

      div {
        margin: 7px 7px;
        font-size: 20px;
      }
    }

    .iframewithedgeAnimate-timetrail {
      position: relative;
      float: left;
      height: 34px;
      width: 490px;
      margin-left: 5px;

      span {
        display: block;
        height: 16px;
        border-radius: 5px;
        cursor: pointer;
      }

      .iframewithedgeAnimate-bar {
        position: relative;
        width: 470px;
        margin: 9px 0px 9px 2px;
        background-color: #FFFFFF;

        .iframewithedgeAnimate-current {
          width: 0;
          background-color: #102A3E;
        }

        .iframewithedgeAnimate-handle {
          position: absolute;
          top: -4px;
          left: -1%;
          width: 20px;
          height: 20px;
          background: #fff;
          border: 2px solid #102A3E;
          border-radius: 5px;
        }
      }
    }
  }
}
