.hotSpot-inner {
  .hotSpot-item {
    position: absolute;
    display: block;
    color: @item-text-color;
    border: 4px solid @item-border-color;
    z-index: 100;
    cursor: pointer;

    .hotSpot-graphic-pin {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      width: 48px;
      height: 48px;
      background-color: @item-text-color;
      border: inherit;
      color:@item-text-color;

         &.radio {
                width: (@icon-size - 6);
                height: (@icon-size - 6);
                border-radius: 50%;
                font-size: (@icon-size - 6);

            }
            &.checkbox {
                border: 3px solid @item-text-color;
                width:(@icon-size - 6);
                height:(@icon-size - 6);
                font-size: (@icon-size - 6);
                font-weight: bold;
                border-radius: 2px;
            }

      .hotSpot-graphic-pin-icon {
        color: @item-color;
        font-size: (@icon-size*2);
        width: auto;
        height: auto;
      }
    }

    &.selected {
        color: @secondary-color;
        border: @secondary-color 4px solid;
        content:" "; /*IE8 fix*/
      .hotSpot-graphic-pin-icon {
        color: inherit;
      }
    }
  }

  .hotSpot-background-image{
    width:100%
  }
}

.hotSpot-widget {
  position: relative;

  &.show-user-answer {
    .incorrect {
      &.selected {
        color:@icon-incorrect-color;
        border:@icon-incorrect-color 3px solid;
        .hotSpot-graphic-pin {
         background: @icon-incorrect-color;
         }
        content:"  "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }

      &.not-selected {
        .hotSpot-graphic-pin {
         background: @item-color;
         }
        color:@item-disabled-color;
        border:@item-disabled-color 3px solid;
        
        content:"   "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }
    }
    .correct {
      &.selected {
        .hotSpot-graphic-pin {
         background:@icon-correct-color;
         }
        color: @icon-correct-color;
        border:@icon-correct-color 4px solid;
        
        content: "    "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;

        }
      }
    }
  }

  &.show-correct-answer {
    .incorrect {
     .hotSpot-graphic-pin {
        background: @item-color;
    }
      color:@item-disabled-color;
      border:@icon-correct-color 3px solid;
   
      content:"     "; /*IE8 fix*/
      .hotSpot-graphic-pin-icon {
        color: inherit;
      }
    }

    .correct {
      .hotSpot-graphic-pin {
        background: @item-color;
      }
      color:@icon-correct-color;
      border:@item-disabled-color 3px solid;

      content:"      "; /*IE8 fix*/
      .hotSpot-graphic-pin-icon {
        color: inherit;
      }
    }

    .selected{
      &.incorrect {
        .hotSpot-graphic-pin {
          background: @icon-incorrect-color ;
        }
        color:@item-disabled-color;
        border:@icon-correct-color 3px solid ;

        content:"     "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }

      &.correct {
        .hotSpot-graphic-pin {
          background: @icon-correct-color ;
        }
        color:red;
        border:@icon-correct-color 3px solid ;

        content:"      "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }
    }
  }

  &.disabled {
  }
}

.no-touch {
  .hotSpot-inner {
    .hotSpot-widget {
      &.before-submit {
        .hotSpot-item {
          &:hover {
            border:@item-hover-color 4px solid;
          }
          .hotSpot-graphic-pin-icon {
            &:hover {
              color: @item-hover-color;
            }
          }
        }
      }

      &.disabled {
        .hotSpot-item {
          &:hover {
            cursor: default;
          }

          .hotSpot-graphic-pin-icon {
            &:hover {
              cursor: default;
            }
          }
        }
      }
    }
  }
}
