.page{
  .article{
    .block{
      .block-title{
        width:100% !important;
        .add-float-left{
          float:left !important;
        }
        .bullet-text-plus{
          border: #102A3E 1px solid;
          background-color: #97c1e0;
          height: 20px;
          width: 20px;
          line-height: 22px;
          margin-left: 10px;
          text-align: center;
          cursor: pointer;
          &.fa-plus{
            color: @primary-color;
            font-size: (@icon-size - 8);
          }
        }
        .bullet-text-minus{
          border: #102A3E 1px solid;
          background-color: #97c1e0;
          height: 20px;
          width: 20px;
          line-height: 22px;
          margin-left: 10px;
          text-align: center;
          cursor: pointer;
          &.fa-minus{
            color: @primary-color;
              font-size: (@icon-size - 8);
          }
        }
      }

      .block-content-items-bullets{
        z-index: 11;
        position: absolute;
        left:10px;
        background-color: #FFFFFF;
        border: 1px #dcddde solid;
        &.no-text-area{
          border:none;
          background-color: transparent;
        }
        .close-minimize-bar{
          padding: 2px;
          background-color:#DCDDDE;
          text-align: right;
          cursor: move;
          .bullet-text-close{
            color: @primary-color;
            cursor: pointer;
            margin-right: 5px;
          }
          .bullet-text-minimize{
            color: @primary-color;
            cursor: pointer;
            margin-right: 5px;
          }
        }
        .bullet-text{
          display: inline-block;
          .bullet-text-inner{
            padding: 15px;
            background-color: #FFFFFF;
            ul li{
              list-style-position: outside;
            }
          }
          .bullet-text-save{
            position: absolute;
            left: 0px;
            bottom: 0px;
            cursor: pointer;
            width: 25px;
            height: 20px;
            background-color: #DCDDDE;
            text-align: center;
            padding-top: 5px;
          }
          .bullet-text-resize{
            position: absolute;
            right: 0px;
            bottom: 0px; 
            cursor:nw-resize;
            width: 23px;
            height: 20px;
            background-color: #DCDDDE;
            text-align: center;
            padding-top: 5px;
            padding-left: 2px;
          }
        }
      }
    }
  }
}
