.bombardier-table{
  table{
    font-size: 16px;
    border: 2px @primary-color solid;
    tr {
        color: darken(@primary-color, 50%);
        th{
          height: 28px;
          line-height: 28px;
          border-bottom: 3px solid @primary-color;
          border-right: 1px @item-color solid;
          text-align: center;
          vertical-align: middle;
          padding: 0px 6px 0px 4px;
          text-transform: uppercase;
        }
        td{
          height: 24px;
          line-height: 24px;
          padding: 0px 4px 0px 4px;
          text-align: left;
          border: 1px @item-color solid;
        }
    }

  }
  .note{
    margin-top: 5px;
    color: darken(@primary-color, 50%);
  }
  .text-font{
    font-family: arial;
    font-size: 20px;
  }
  .warning{
    color:#EC1C24;
    .text-font;
  }
  .caution{
    color: #FFE400;
    .text-font;
  }
  .advisory{
    color: #0094D8;
    .text-font;
  }
  .status{
    color: #ffffff;
    .text-font;
  }
}
