#wrapper{
  .navigation{
      .TOC-navigation-icon{
        position: relative;
        float: right;
        margin-top: 14px;
        margin-left: -20px;
        margin-right: 13px;
        cursor: pointer;
      }
  }
  .TOC-container{
    &.modal{
      width: 100%;
      height:100%;
      max-height:100%;
      max-width:100%;
      .content{
        overflow-x: hidden;
      }
    }
    .content{
      border: none !important;
      background-color:lighten(@secondary-color,  30%) ;
      overflow: auto;
      width:30%;
      &.not-large{
        width:35%;
      }
      .title{
        background-color: @navigation-color ;
        color: @font-color-inverted;
        padding: @item-padding (@item-padding * 4);
          .TOC-title h3{
            margin-bottom: 10px;
          }
      }
      height: 100%;
      float: right;
      overflow-y: auto;
      overflow-x: hidden;
      right: -320px;
      transition: all 1s !important;
      -webkit-transition: all 1s !important;
      -ms-transition: all 1s !important;
      -moz-transition: all 1s !important;
      .head .close{
        font-size:@icon-size;
        line-height: (@icon-size + 2);
        color: @item-text-color !important;
        border: none !important;
        background-color: transparent;
        text-decoration: none;
        box-shadow: none;
        right: 10px !important;
        top: 5px !important;
      }
      .body{
        background-color:#102a3e;
        color: white;
         display: inline-table;
        .title{
          background-color:#102a3e;
          border-bottom: 1px inset #ffffff;
        }
        .TOC-drawer{
          margin-top: 10px;
          .TOCDrawer-inner{
            width: 100%;
            padding: 5px 5px 8px 2px;
            display: inline-flex;
            cursor: pointer;
            &.highlight-current-block{
              background-color: #8996A0;
            }
            .TOC-identifier-graphic{
              position: relative;
              float: left;
            }
            .TOC-identifier-text{
              width:30px;
              height: 25px;
              position:relative;
              float: left;
              font-weight: 700;
              font-size: 16px;
              color:white;
              text-align: center;
              line-height: 25px;
              background-color: #8996A0;
              border-top-right-radius: 3px;
              border-bottom-right-radius: 3px;
            }
            .TOC-title{
              font-size: 18px;
              float: left;
              line-height: 20px;
              width: 70%;
              margin-top: 4px;
            }
            .TOC-progress{
              width: 10%;
              float: left;
              position: relative;
              .TOC-progress-icon {
                outline: none;
                text-decoration: none;
                border-color: #FFF;
                display: block;
                height: 13px;
                border-radius: 3px;
                color: #FFF;
                height: 20px;
                width:20px;
                margin: 5px;
                color: rgb(255,255,255);
                background-color: rgb(255,255,255);
                border-radius: 100%;
                &.visited {
                  background-color: rgb(51,201,53);
                  color: rgb(51,201,53);
                  border-radius: 100%;
                }
              }
            }
            &.has-identifier{
              .TOC-title{
                width: 60%;
              }
              .TOC-progress{
                width: 10%;
              }
              .TOC-identifier-container{
                float: left;
                position: relative;
                margin-left: 40px;
              }
            }
          }
        }
      }
    }
  }
  .modal-overlay{
    background:rgba(255,255,255,0.8);
  }
}

