.block-identifier-inner{
    position: relative;
    text-align: right;


    .block-identifier-item{
      width: 65px;
      height: 25px;
      margin-top: 20px;
      display: inline-block;
      z-index:1;
      .block-identifier-graphic{
        position: relative;
        float: left;
        width: 30px;
        height: 25px;
        margin-right: 2px;
        img{
          display: block;
          height: 25px;
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
        }
      }

      .block-identifier-text{
        width:30px;
        height: 25px;
        position:relative;
        float: left;
        font-weight: 700;
        font-size: 16px;
        color:white;
        text-align: center;
        line-height: 25px;
        background-color: #8996A0;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
      }
    }

}
