 // Styling navigation drawer.
 @navigationDrawer-height: 50px;
 @navigationDrawer-height-mobile: 0px;
 @navigationDrawer-bg-color: @primary-color;
 @navigationDrawer-border: @navigation-border;

.navigationDrawer {

    position: fixed;
    z-index: 200;

    height: @navigationDrawer-height;
    width: 100%;

    //background-color: @navigationDrawer-bg-color;
    text-align: right;

    color: @foreground-color;

    -webkit-transform: translate(0px);
    -moz-transform: translate(0px);
    -o-transform: translate(0px);
    transform: translate(0px);
    -webkit-transition: -webkit-transform 400ms ease;
    -moz-transition: -moz-transform 400ms ease;
    -o-transition: -o-transform 400ms ease;
    transition: transform 400ms ease;
    -webkit-transition-property: -webkit-transform,left,right;
    -webkit-backface-visibility: hidden;

    &.open-from-top {
        transform: translateY(@navigationDrawer-height);
    }
    &.open-from-bottom {
        transform: translateY(-1 * @navigationDrawer-height);
    }
    &.active {
      &.open-from-top, &.open-from-bottom {
          transform: translateY(0px);
      }
    }

    .navigationDrawer-inner {
        display: block;
        padding: 5px;

        display: inline-block;
        text-align: center;

        .button-container {
            float: none !important;
            background: none !important;
            list-style: none;
            overflow: hidden;
            //margin: 0 0 0 10px !important;
            //padding: 10px 0 !important;
            -moz-user-select: -moz-none;
            -khtml-user-select: none;
            -webkit-user-select: none;
            -o-user-select: none;
            user-select: none;
            display: block;

            /**:before {
                content: " ";
                display: table;
            }*/

            *:after {
                content: " ";
                display: table;

                clear: both;
            }

            li {
                list-style: none !important;
                background: none !important;
                float: left !important;
                margin: 0 !important;
                padding: 5px !important;
                position: relative;
                cursor: pointer;
                box-sizing: content-box;
                overflow: visible;

                height: 40px;
                width: 60px !important;
                padding: 5px 0px 0px 0px !important;

                &:before {
                    content: "" !important;
                    padding: 0;
                    margin: 0;
                }

                a {
                    position: absolute;
                    left: 10px;
                    top: 25px;
                    //height: 30px;
                    border-radius: 4px 4px 0 0;

                    //border-radius: 4px;
                    margin: 0 !important;
                    display: block;
                    //position: relative;
                    width: 40px;
                    height: 40px;
                    padding: 8px 0px;

                    box-shadow: inset 0 0 100px rgba(255,255,255,0.2);
                    background: @primary-color !important;

                    &:before {
                        color: @secondary-color;
                        height: 24px;
                        width: 24px;
                        font-size: 24px !important;
                        text-decoration: none;
                        text-transform: none;
                        speak: none;
                        -webkit-transform: translateZ(0);
                    }

                    -webkit-transform: translate(0px);
                    -moz-transform: translate(0px);
                    -o-transform: translate(0px);
                    transform: translate(0px);
                    -webkit-transition: -webkit-transform 400ms ease;
                    -moz-transition: -moz-transform 400ms ease;
                    -o-transition: -o-transform 400ms ease;
                    transition: transform 400ms ease;
                    -webkit-transition-property: -webkit-transform,left,right;
                    -webkit-backface-visibility: hidden;
                }

                &:hover {
                    a {
                        //top: 5px;
                        transform: translateY(-20px);
                        opacity: 1;
                        //height: 35px;
                    }
                }

                &:after {
                    background: url(data:image/png;base64,) 0 100% no-repeat;
                    content: " ";
                    height: 15px;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    display: block;
                    width: 60px;
                    z-index: 100;
                }
            }
        }
    }
}

.navigation-drawer-control {
    float: right;
}
