.focalpointGenerator-component {

    .focalpointGenerator-graphic-pin {
        display: block;
        position: absolute;
        border: 2px solid #00A2D3;
        border-radius: 5px;

        &.selected{
            background-color: rgba(224,234,234,0.3);
            border: 2px solid #7df9ff;
        }

        &.visited {

            .focalpointGenerator-graphic-pin-icon {
                color: @item-visited-color;

            }
        }
    }

    .focalpointGenerator-graphic-pin-icon {
        color: @item-color;
        font-size: (@icon-size*2)+2;
        width:auto;
        height:auto;
    }


    .focalpointGenerator-body-container{
        width: 100%;
        height: 100%;

    }

    .focalpointGenerator-highlight{
        position: absolute;
        height: 100%;
        width: 100%;
        top:0px;
    }

    .focalpointGenerator-highlight-body{
        background-color: white;
        border: solid gray 2px;
        border-radius: 5px;
        margin-left: 0px;
        //margin-top: 29px;
        text-align: center;
        z-index: 5;
        //padding: 5px;
        padding: 0px 8px;
        max-width: 100%;

        .focalpointGenerator-item-body-inner{
            font-size: 16px;
        }
    }

    .focalpointGenerator-button-icon{
        margin: 10px;
        left: -1px;
        top: 0px;
        right: auto;
        bottom: auto;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.0980392) 3px 3px 3px 0px;
        border: 2px solid rgb(219, 220, 221);
        text-align: center;
        color: #2F2D2D;
        //padding: 10px;
        padding: 10px 2px;
        
        background-color: white;
        margin: 5px 5px 5px 5px !important;
        color: @primary-color;
        &.selected{

            background-color: grey;
            color: white;

        }
    }

    .dragItem{
        position:fixed;
    }



    &.desktop {
        .focalpointGenerator-slide-container {

            float: left;
            .dir-rtl & {
                float: right;
            }
            margin-right: 3%;
            .dir-rtl & {
                margin-right: inherit;
                margin-left: 3%;
            }
            width: 60%;
        }
        .focalpointGenerator-slider-graphic {
            margin-right: 0;
            width:60%;
            display: none;
        }
        .focalpointGenerator-strapline {
            display:none;
        }

        .focalpointGenerator-button-container{
            width: 25%;
            display: block;
            float: left;


        }
        .focalpointGenerator-image-container{
            width: 74%;
            text-align: center;
            display: block;
            float: right;
            position: relative;

        }

        .focalpointGenerator-graphic-pin{
            display: none;
        }
    }

    .half_view{
        .focalpointGenerator-slide-container {

            float: left;
            .dir-rtl & {
                float: right;
            }
            margin-right: 3%;
            .dir-rtl & {
                margin-right: inherit;
                margin-left: 3%;
            }
            width: 60%;
        }
        .focalpointGenerator-slider-graphic {
            margin-right: 0;
            width:60%;
            display: none;
        }
        .focalpointGenerator-strapline {
            display:none;
        }

        .focalpointGenerator-button-container{
            width: 25%;
            display: block;
            float: left;


        }
        .focalpointGenerator-image-container{
            width: 75%;
            display: block;
            float: right;

        }

    }

    &.mobile {

        .focalpointGenerator-image-container{
            width: 100%;
            text-align: center;
            display: block;
            float: right;
            position: relative;

        }
        .focalpointGenerator-slider-graphic {

            width:50%;

        }

        .focalpointGenerator-highlight {

            display: none;

        }

        .highlight-shadow{

            display: none;

        }


    }

    .focalpointGenerator-slide-container {
        width: 100%;
        position: relative;
        overflow-x: hidden;
        float: left;
        .dir-rtl & {
            float: right;
        }
    }

    .focalpointGenerator-slider-graphic {
        float: left;
        .dir-rtl & {
            float: right;
        }
        position: relative;
    }

    .focalpointGenerator-item-title {
        position:relative;
        display:block;
    }

    .focalpointGenerator-controls {
        display: block;
        width: (@icon-size*2);
        height: (@icon-size*2);
        position: absolute;
        top: 50%;
        margin-top: -(@icon-size*2);
        z-index: 10;
        text-decoration: none;
        background-color:@button-color;
        &:hover {
            background-color:@button-hover-color;
            .icon {
                color:@button-text-hover-color;
            }
        }
        .icon {
            margin: (@icon-size/2);
            color:@button-text-color;
        }
    }

    .focalpointGenerator-control-left {
        left: 0;
        .dir-rtl & {
            left: inherit;
            right: 0;
        }
    }

    .focalpointGenerator-control-right {
        right: 0;
        .dir-rtl & {
            right: inherit;
            left: 0;
        }
    }

    .focalpointGenerator-strapline {
        height: auto;
        position: relative;
        float: left;
    }

    .focalpointGenerator-strapline-header {
        overflow: hidden;
        position: relative;
    }

    .focalpointGenerator-strapline-header-inner {
        position:relative;
    }
    .focalpointGenerator-strapline-title {
        position: relative;
        float: left;
        text-decoration: none;
        background-color: @button-color;
        color: @button-text-color;
        .dir-rtl & {
            float: right;
        }
        .icon {
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            padding: 5px;
            color: @button-text-color;

        }

    }

    .focalpointGenerator-strapline-title-inner {
        height: (@item-padding-top + @item-padding-bottom + @icon-size);
        line-height: (@item-padding-top + @item-padding-bottom + @icon-size);
        padding-left: (@icon-size/2);
        padding-right: (@item-padding-left + @item-padding-right + @icon-size);
        .dir-rtl & {
            padding-right: (@icon-size/2);
            padding-left: (@item-padding-left + @item-padding-right + @icon-size);
        }
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }

    .focalpointGenerator-popup-open {
        display: block;
        position: absolute;
        top:0;
        right:0;
        .dir-rtl & {
            right:inherit;
            left:0;
        }
        text-decoration: none;
        z-index: 5;
        background-color: @inverted-background-color;
        padding: @item-padding;
        .icon {
            color: @inverted-foreground-color;
            &:hover {
                color: lighten(@inverted-background-color, 30%);
            }
        }
    }

    .focalpointGenerator-hidden {
        display:none;
    }

    .focalpointGenerator-item-graphic {
        width: 100%;
        text-align: center;
        margin-top: 5px;
    }

    .focalpointGenerator-item-graphic-inner img{
        padding: 0px 0px 0px 0px;
        border: #00a2d3 solid 3px;
        border-radius: 5px;
        position:absolute;
        
        .dir-rtl & {
            padding-left: inherit;
            padding-right: @item-padding-left;
        }
    }

    .focalpointGenerator-popup-done {
        color: @item-text-color;
        position: absolute;
        right: 13px;

        .dir-rtl & {
            right: inherit;
            left: 13px;
        }
    }


    .popup-shadow {
        display: none;
        position: fixed;
        top:0px;
        left:0px;
        .dir-rtl & {
            left: inherit;
            right: 0px;
        }
        width:100%;
        height:100%;
        z-index:549;
        .no-opacity & {
            background-image:@transparency-fallback-png;
        }
        background-color: @transparency;
    }

    .highlight-shadow {
        display: none;
        position:absolute;
        .dir-rtl & {
            left: inherit;
            right: 0px;
        }
        width:100%;
        height:100%;
        .no-opacity & {
            background-image:@transparency-fallback-png;
        }
        background-color: @transparency;
    }

    a{
        text-decoration: none;
    }


    .focalpointGenerator-image-container .focalpointGenerator-item-graphic-inner {
        overflow: hidden;
        border: #00a2d3 solid 3px;
        border-radius: 5px;
    }

    .focalpointGenerator-background-image {
        width:auto;
    }

}

.no-touch {
    .focalpointGenerator-component {

        .focalpointGenerator-graphic-pin-icon {
            &:hover {
                color: @item-hover-color;
            }
        }
        .focalpointGenerator-strapline-title {
            &:hover {
                background-color: @button-hover-color;
                color: @button-text-hover-color;
                .icon {
                    color: @button-text-hover-color;
                }

            }
        }
        .focalpointGenerator-popup-close {
            &:hover {
                color: @item-hover-color;
            }
        }
    }
}

@media all and (max-width:960px) {
    .focalpointGenerator-component {
        .focalpointGenerator-highlight-body {
            .focalpointGenerator-item-body-inner {
                font-size: 12px;
            }
        }
    }
}



