//================================================================
//eLearning modifications for xLarge breakpoint (1800px)

@media all and (min-width: @device-width-xlarge) {
  .page .page-title {
    font-size: 2.5em !important;
  }

  .article .article-title-inner {
    font-size: 1.3em !important;
  }

  .block {
    .block-inner {
      .block-title .block-title-inner {
        font-size: 1.25em !important;
      }
    }
  }

  .graphic-widget {
    img {
      width: 100% !important;
    }
  }

  .iframewithedgeAnimate-inner {
    .iframewithedgeAnimate-iframe.scale {
      -webkit-transform-origin: 50% 0% 0 !important;
    }
  }

  .gmcq-component .gmcq-item label {
    img{
      width: 100% !important;
    }
  }
  .gmcq-component .gmcq-item-inner {
    font-size: 1.3em !important;
  }

  .component .component-inner {
    font-size: 1.2em !important;
    line-height: 120% !important;
    img {
      width: 100% !important;
    }
  }
  .component .component-instructions {
    font-size: 1em !important;
  }

  .focalpointGenerator-component.desktop {
    .focalpointGenerator-image-container {
      width: 55% !important;
    }
    .focalpointGenerator-button-container {
      width: 40% !important;
    }
  }

  .dragAndDrop-component .dragAndDrop-inner{
    .dragAndDrop-droppables-container {
      text-align: center !important;
    }
    .dragAndDrop-draggables-container {
      text-align: center !important;
    }
  }

  .embeddedLink-inner .embeddedLink-description-container {
    .pagination-controller-bar .embeddedLink-pagination-controls .count-container {
      line-height: 33px !important;
      font-size: 1.1em !important;
    }
  }
}
