// Navigation styling
// ==================

.navigation {
    width: 100%;
    overflow: hidden;
    background-color: @navigation-color;
    /*box-shadow: 0 2px 5px rgba(0,0,0,0.26);
    */
    height: (@navigation-padding * 2) + @icon-size;

    .ie8 & {
        max-width: @ie8-max-width;
    }

    .icon {
        padding: @navigation-padding;
        color: @navigation-icon-color;
        .transition-color;

        .no-touch &:hover {
            color: @navigation-icon-color-hover;
        }
    }
}

// Top Bar EOM Modifications
.navigation {
  width: 100%;
  height: auto;
  }

.navigation, .bottomnavigation {
  .logo {
    padding: 0px;
    float: left;
  }
  .icon, .fa {
    color: @navigation-progress-color !important;
    width: @icon-size !important;
    font-size: @icon-size !important;
    text-decoration: none !important;
    text-transform: none !important;
    speak: none !important;
   }
}

.navigation .navigation-drawer-toggle-button {
  float: right !important;
  padding-top: 13px !important;
  padding-right: 40px !important
}


// Bottom Bar EOM Modifications

.centerd-buttons {
  left: 50%;
  position: absolute;
  top: 0em;

  .centered-buttons-wrapper {
    float: left;
    position: relative;
    right: 50%;

    .bottomnavigation & {
      padding: 13px;
    }
  }
}

//for aircraft program of bottom nav
html.has-bottomnavigation .bottomnavigation {
  background-color: #0069AA;
  background-repeat: no-repeat;
  background-position: top left;
  height: @bottomnavigation-height;
  border-top: 0px solid @primary-color !important;
}

