.page-level-progress-navigation-completion {
    width: 144px !important;
    height: 14px !important;
    top: -3px;
    overflow: hidden;
    border: 2px solid @background-color;
    position: relative;
    -webkit-border-radius: 8px !important;
    -moz-border-radius: 8px !important;
    border-radius: 8px !important;
}

.page-level-progress-navigation-bar {
    height: 100%;
    position: absolute;
    border:none !important;
    bottom: 0;
    background-color: @primary-color;
    overflow: hidden;
}
.page-level-progress-numericCount-bar{
    width:100%;
    text-align:center;
    font-size:16px;
    font-weight:600;
    font-family:sans-serif;
    top: -1px;
    color: @primary-color;
    position: absolute;
}
.page-level-progress-numericCount-bar-mask{
    width: 144px;
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    font-family: sans-serif;
    top: -1px;
    color: @item-text-color;
    position: absolute;
    overflow: hidden;
}