body{
  overflow: hidden !important;
}

.labelGenerator-inner {
  .labelGenerator-widget {
    position: relative;
    margin: 0 auto;
    .labelGenerator-background-image{
      width: 100%;
      height: auto;
    }
    .labelGenerator-item-container{
      .labelGenerator-highlight{
         position: absolute;
         border:3px solid #00A2D3;
         box-shadow: #102a3e 3px 3px 2px 0;
         border-radius: 5px;
        z-index: 0;
         &.hideHighlight{
           display: none;
         }
       }
      .labelGenerator-item{
        position: absolute;

        &.hideDescription{
          display: none;
        }

        .labelGenerator-graphic-pin {
          width: 10px;
          height: 10px;
          position: absolute;
          display: block;
          border-radius:50%;
          z-index:10;
          background-color: #00A2D3;
          box-shadow: 1px 2px 9px 0 #0f2a3e;
          //padding: 5px;
          cursor:pointer;
        }
        .icon{
          color: @secondary-color-inverted;
        }
        .iconPlus{
          //color: #00A2D3;
          color: #102A3E;
          font-size: 19px;
        }
        .plus{
          text-align: center;
          position: relative;
          top: 7px;
          left: 1px;
        }

        .labelGenerator-graphic-pin-icon {
          width: 35px;
          height: 35px;
        }

        .labelGenerator-popup-container{

     /*     .popup-line{
            height: 2px;
            position: relative;
            float: left;
            box-shadow: 0px 1px 2px #102A3E;
          }*/
          .popup-open{
            position: relative;
            margin-right: 0px;
            margin-top: -10px;
            .popup-text{
              text-align: left;
            }
          }

          .popup-line{
            background-color: #00A2D3;
            height:2px;
            position: relative;
            top: 4px;
            left: 4px;
            float: left;
            box-shadow: 0px 1px 2px #102A3E;
          }
          &.right{
            .popup-open {
              border-left: 2px #00A2D3 solid;
              border-top: 2px #00A2D3 solid;
            }
          }

          &.left{

            .popup-open {
              border-right: 2px #00A2D3 solid;
              border-bottom: 2px #00A2D3 solid;
            }

          }
          &.top{

              .popup-line{
                top:5px;
              }
            .popup-open{
              border-bottom: 2px #00A2D3 solid;
              border-right: 2px #00A2D3 solid;
            }

          }
          &.bottom{

            .popup-open{
              border-top: 2px #00A2D3 solid;
              border-left: 2px #00A2D3 solid;
            }
          }
           &.top-right{
            .popup-open{
              border-bottom: 2px #00A2D3 solid;
              border-left: 2px #00A2D3 solid;
             }
           }
          &.top-left{
            .popup-open{
              border-right: 2px #00A2D3 solid;
              border-bottom: 2px #00A2D3 solid;
            }
          }

          &.bottom-right{

            .popup-open{
              border-top: 2px #00A2D3 solid;
              border-left: 2px #00A2D3 solid;
            }
          }
          &.bottom-left{
              .popup-open{
                border-top: 2px #00A2D3 solid;
                border-right: 2px #00A2D3 solid;
             }
          }

          .popup-open{
            display: inline-block;
            border-radius:5px;
            position: relative;
            font-size: 16px;
            text-align: center;
            font-weight: bold;
            z-index: 5;
            padding: 5px;
            max-width: 250px;
            border: 2px #DCDDDE solid;
            background-color: @foreground-color-inverted;

            .popup-with-add-sign{
              float: right;
              top: 3px;
              position: absolute;
              right: 0px;

              &:hover {
                cursor: pointer;
              }
            }
          }
        }
      }
    }
    .highlight-moreDescription-container{
      position: absolute;
      top: 0px;
      width: 100%;
      .popup-with-add-sign-open{
        width: 50%;
        max-width:530px;
        display: inline-table;
        padding:10px;
        position: absolute;
        background-color: #fff;
        border:1px solid @primary-color;
        border-radius: 10px;
        z-index:15;
        top: 0px;
        margin: 0 auto;
        bottom: 0px;
        //right: 36px;
        left: 25%;
        cursor: pointer;
        &.hideMoreDescription{
          display:none;
        }
        .popup-item-body{
          border: 2px solid rgb(219, 220, 221);
          -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
          border-radius: 5px;
          border:1px solid @foreground-color;
          margin-bottom: 5px;
          padding: 10px;
          cursor: pointer;
        }
        .popup-with-add-sign-image{
          border: 2px solid rgb(219, 220, 221);
          -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
          margin-bottom: 5px;
          border-radius: 5px;
          border: 1px solid #dbdcdd;
          padding: 5px;
          cursor: pointer;
        }
        .moreDescription-container-close{
          cursor: pointer;
          //margin-right: -26px;
          color: #ffffff;
          float: right;
          //font-size: 19px;
          //top: 0px;
          //right: 0px;
          top: 4px;
          right: 4px;
          position: absolute;
          //background: #00A2D3;
          background: #102A3E;
          border-radius: 100%;
          padding: 3px 5px;
        }
      }
    }

    .showHideAll{
      width:72px;
      background-color: #FFFFFF;
      color: #102A3E;
      padding: 5px 10px 3px 10px;
      border: 2px solid #DCDDDE;
      font-weight: 400;
      border-radius: 7px;
      float: right;
      text-align: center;
      cursor: pointer;
    }

  }
}
