#wrapper{
   overflow-y: hidden;
}

.verticalBlockSlider-article {
     height:890px;
    .verticalBlockSlider-container {
        overflow: hidden;
        .verticalBlockSlider {
            .block {
                position: relative;
                display: block;
                height: 880px;
            }
        }
    }
    .verticalBlockSlider-controls-container {
        position: fixed;
        display: table;
        height: 88%;
        right: 0.2%;
        list-style-type: none;
         .verticalBlockSlider-controls-container-inner{
           display: table-cell;
           vertical-align: middle;

           .verticalBlockSlider-controls-inner {
             margin-left: auto;
             margin-right: auto;
         
           }
         }
    }
    .verticalBlockSlider-text-tab {
        outline: none;
        text-decoration: none;
        padding: @item-padding;
        color: @item-text-color;
        background-color: @item-color;
        margin-top: 8px;
        margin-right: 5px;
        &.active {
            background-color: @item-selected-color;
            color: @item-text-selected-color;
        }
    }
    .fa.fa-arrow-down, .fa-arrow-up {
        text-decoration: none;
        color: @primary-color;
    }
    .verticalBlockSlider-progress {
        outline: none;
        text-decoration: none;
        border-color: @primary-color;
        display: block;
        height: 13px;
        border-radius: 100px;
        color: @primary-color;
        
        &:hover {
            background-color: @primary-color;
        }
        margin: 3px;
        &.visited {
            background-color: #97c1e0;
            color: @primary-color;
        }
        &.active {
            background-color: @primary-color;
            color: @primary-color;
        }
    }
    .verticalBlockSlider-control-left {
        right: 5px;
        top: 60px;
        position: fixed;
    }
    .verticalBlockSlider-control-right {
        right: 5px;
        position: fixed;
        bottom: 0px;
        z-index: 999;
    }
    .verticalBlockSlider-hidden {
        display: none;
        visibility: hidden;
    }
}

.no-touch {
    .verticalBlockSlider-controls {
        &:hover {
            .icon {
                color: @item-text-hover-color;
            }
        }
    }
    .verticalBlockSlider-text-tab {
        &:hover {
            text-color: @item-text-hover-color;
            background-color: @item-hover-color;
        }
    }
    .verticalBlockSlider-icon-tab {
       &:hover {
            .icon {
                color: @secondary-color;
            }
        }
    }
}

