    bottom:0px;
   }
   .reset-graphic-btn{
      position:absolute;
      bottom:0px;
   }
    .buttons {
        margin: 0 0 0 !important;
        background-color: #8996a0;
        padding: 6px !important;
        border-bottom: 1px solid #102a3e;
        border-right: 1px solid #102a3e;
