.didYouKnow-button-container {
    margin: 20px 0 10px 0;

    button.didYouKnow-button {
        height: 38px;
        background-color: #102A3E;
        border: 2px solid #DCDDDE;
        font-size: 16px;
        color: #ffffff;
        padding: 5px 20px;

        .no-touch &:hover {
            background-color: #102A3E;
        }
    }
}

.didYouKnow-popup {
    position: absolute;
    top: 25%;
    left: 50%;
    z-index: 11;

    .didYouKnow-popup-inner {
        position: relative;
        left: -50%;
        width: 610px;
        max-height: 520px;
        background-color: #ffffff;
        border: 2px solid #102A3E;
        font-size: 16px;
        overflow: hidden;

        .popup-toolbar {
            height: 36px;
            color: #ffffff;
            background-color: #102A3E;
            cursor: move;

            .popup-toolbar-inner {
                position: relative;

                .popup-title {
                    padding: 8px 10px;
                }

                button.popup-close {
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    padding: 5px;

                    .icon {
                        color: #ffffff;
                    }
                }
            }
        }

        .popup-content {
            .popup-body {
                color: #102A3E;
                padding: 10px 20px;
            }

            .popup-graphic {
                padding: 0px 10px 10px 10px;
            }
        }
    }

}

.block {
    &.overflow-hidden {
        overflow: hidden;
    }
}
