// Component styling
// =================

.component {
    .component-inner {
        .component-background;
        &.blank-inner { // don't apply styling to blank component
            background-color: transparent;
            padding: 0;
        }
    }

    .component-title{
        margin-bottom: @component-title-margin-bottom;
        color: @title-color;
        text-align: left;

        .dir-rtl & {
            text-align: right;
        }
    }

    .component-title-inner {
        padding: 0px;
    }

    .component-body{
        margin: @component-body-margin-top 0px @component-body-margin-bottom;

        a {
          text-decoration: underline;
          color: @font-color;
        }
    }

    .component-body-inner{
        padding: 0px;
    }

    .component-instruction {
        margin-bottom: @component-instruction-margin-bottom;
        .instruction-text;
    }

    .component-instruction-inner {
        &.validation-error {
            color: @validation-error;
        }
    }

    // to replicate padding between blocks when components drop into a single column
    @media all and (max-width: (@device-width-small)) {

        &.component-left {
            padding: 0 0 (@block-padding / 3);
        }

        &.component-right {
            padding: (@block-padding / 3) 0 0;
        }
    }

}


// Custom EOM Modifications

.component
  .component-inner {
    background-color: transparent !important;
    padding: 0px !important;
    @media all and (min-width: @device-width-xlarge) {
        font-size: 1.2em !important;
        line-height: 120% !important;
        img {
          width: 100% !important;
    }
  }
}

.component .component-inner {
  img{
    display: block !important;
    margin-left: auto !important;
    margin-right: auto !important;
    max-width: 100% !important;
  }
}

.component-left .component-inner{
        margin-right: 0% !important;
        padding-top: 15px !important;
        padding-right: 10px !important;
    }


.component-right .component-inner{
        margin-left: 0% !important;
        padding-top: 15px !important;
        padding-left: 10px !important;
    }

.component .component-body-inner {
    line-height: @item-padding !important;
  }

.component .component-instruction {
  line-height: 120% !important;
  @media all and (min-width: @device-width-xlarge) {
    font-size: 0.9em !important;
  }
}

// responsiveIframe EOM Modifications

.responsiveIframe-component {
  &.component-left {
    @media all and (min-width:(@device-width-medium + 1px)) {
      .component-inner {
        margin-right: 10px;
      }
    }
  }
  &.component-right {
    @media all and (min-width: (@device-width-medium + 1px)) {
      .component-inner {
        margin-left: 10px;
      }
    }
  }
}

.responsiveIframe-component .responsiveIframe-Iframe-holder {
  iframe {
    background: url('assets/ajax-loader.gif') center center no-repeat;
  }
}


// Label Generator EOM Modifications

@media all and (max-width:(@device-width-xlarge - 1px)) and (min-width:(@device-width-large + 1px)) {
