  padding: 7px 0 7px 0 !important;
  box-shadow: 3px 2px 1px 0 rgba(0,0,0,0.2);
  border-bottom: 1px solid @primary-color;
  border-right: 1px solid @primary-color;
  margin-top: 5px !important;
  @media all and (min-width: @device-width-xlarge) {
    font-size: 1.1em !important;
    width: 110px !important;
  }
}


.labelGenerator-inner .labelGenerator-widget .highlight-moreDescription-container {
  .labelGenerator-highlight {
    position: absolute !important;
    border: #00A2D3 solid 3px !important;
    box-shadow: @primary-color 3px 3px 2px 0;
    border-radius: 5px;
    z-index: 0 !important;
  }
  .popup-with-add-sign-open {
    max-width: 600px !important;
    border: 2px solid @primary-color !important;
    margin-top: 20px !important;
    @media all and (min-width: @device-width-xlarge) {
      max-width: 680px !important;
      margin-top: 0px !important;
    }
    .popup-item-body{
      border: 2px solid @light-grey !important;
      @media all and (min-width: @device-width-xlarge) {
        font-size: 1.1em !important;
        padding: 12px !important
      }
    }
    .popup-with-add-sign-image{
      border: 2px solid @light-grey !important;
      text-align: center !important;
      img{
        width: 520px !important;
        @media all and (min-width: @device-width-xlarge) {
          width: 650px !important;
          padding: 20px 0 20px 0 !important;
        }
      }
    }
  }
}

.labelGenerator-inner .labelGenerator-widget .labelGenerator-item-container .labelGenerator-item {
  .labelGenerator-graphic-pin{
    box-shadow: @primary-color 1px 2px !important;
  }
  .labelGenerator-graphic-pin-icon{
    width: 1.35em !important;
    height: 1.35em !important;
  }
  .plus {
    top: 5px !important;
    left: 0px !important;
  }
  .labelGenerator-popup-container .popup-open {
    max-width: 250px !important;
    z-index: 11 !important;
    font-size: 1em !important;
    /*height: 20px !important;*/
    @media all and (min-width: @device-width-xlarge) {
      margin-left: -285px !important;
      height: 25px !important;
      max-width: 550px !important;
      font-size: 1.1em !important;
    }
    .popup-text{
      padding: 2px !important;
      @media all and (min-width: @device-width-xlarge) {
        margin-right: 23px !important;
      }
    }
  }
  .labelGenerator-popup-container.right .popup-open {
    @media all and (min-width: @device-width-xlarge) {
      margin-left: 50px !important;
    }
  }
  .iconPlus {
    font-size: 1.25em !important;
  }
  .icon{
    font-size: 1.5em !important;
  }
}

// Embedded Link EOM Modifications

.page .page-body {
