  font-size: 1.25em !important;
}

.embeddedLink-inner {
  .icon {
    color: @primary-color !important;
    font-size: 1.5em !important;
    @media all and (min-width: @device-width-xlarge) {
      font-size: 1.3em !important;
    }
  }

  .icon-sound {
    .fa();
    .fa-lg();

    &:before {
      content: "\f028";
    }
  }

  .icon-sound-mute {
    .fa();
    .fa-lg();

    &:before {
      content:"\f026"
    }
  }
