// HotSpot EOM Modifications

.hotSpot-inner {
  .hotSpot-item {
    position: absolute;
    display: block;
    color: @primary-color;
    border: 3px solid #00A2D3;
    border-radius: 3px;
    z-index: 100;
    cursor: pointer;
    box-shadow: @primary-color 3px 3px 2px 1px;

    .hotSpot-graphic-pin {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      width: 15px;
      height: 15px;
      background-color: @primary-color;
      border: 4px solid @background-color;
      box-shadow: 0 0 0 2px @primary-color;


      &.single-answer{
        border-radius: 50% !important;
      }

      &.multiple-answer{
        border-radius: 5px !important;
      }

      .hotSpot-graphic-pin-icon {
        color: @item-color;
        font-size: (@icon-size);
        width: 35px;
        height: 35px;
        text-align: center;
        position: absolute;
        top: 7px;
      }

      .icon-pin:before {
          content: " ";
      }
    }

    &.selected {
        color: #00A2D3;
        border: #00A2D3 3px solid;
        content:" "; /*IE8 fix*/
      .hotSpot-graphic-pin {
        background: #00A2D3;
        border: @background-color 4px solid;
        box-shadow: 0 0 0 2px @primary-color;
      }
    }
  }

  .hotSpot-background-image{
    width:100%
  }
}

.hotSpot-widget {
  position: relative;

  &.show-user-answer {
    .incorrect {
      &.selected {
        color:@icon-incorrect-color;
        border:@icon-incorrect-color 3px solid;
        content:"  "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }

      &.not-selected {
        color:@secondary-color;
        border:@secondary-color 3px solid;
        content:"   "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }
    }
    .correct {
      &.selected {
        color: @icon-correct-color;
        border:@icon-correct-color 3px solid;
        content: "    "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }
    }
  }

  &.show-correct-answer {
    .incorrect {
      color:@secondary-color;
      border:@secondary-color 3px solid;
      content:"     "; /*IE8 fix*/
      .hotSpot-graphic-pin-icon {
        color: inherit;
      }
    }
    .correct {
      color:@icon-correct-color;
      border:@icon-correct-color 3px solid;
      content:"      "; /*IE8 fix*/
      .hotSpot-graphic-pin-icon {
        color: inherit;
      }
    }
    .selected{
      &.incorrect {
        .hotSpot-graphic-pin {
          background: @icon-incorrect-color;
        }
        color:@item-color;
        border:@item-color 3px solid ;

        content:"     "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }

      &.correct {
        .hotSpot-graphic-pin {
          background: @icon-correct-color;
        }
        color:@icon-correct-color;
        border:@icon-correct-color 3px solid;

        content:"      "; /*IE8 fix*/
        .hotSpot-graphic-pin-icon {
          color: inherit;
        }
      }





    }
  }

  &.disabled {
  }
}

.no-touch {
  .hotSpot-inner {
    .hotSpot-widget {
      &.before-submit {
        .hotSpot-item {
          &:hover {
            border: #00A2D3 3px solid;
          }
          .hotSpot-graphic-pin-icon {
            &:hover {
              color: @item-hover-color;
            }
          }
        }
      }

      &.disabled {
        .hotSpot-item {
          &:hover {
            cursor: default;
          }

          .hotSpot-graphic-pin-icon {
            &:hover {
              cursor: default;
            }
          }
        }
      }
    }
  }
}
