.progressTracking {
    right: 0px;
    top: 5px;
    .progressTracking-inner {
        .progressTracking-icon {
            width: 40px;
            height: 40px;
            background: #ffffff;
            border-radius: 50%;
            position: absolute;
            right: 30px;
            top: 20px;
            text-align: center;
            line-height: 40px;
            color: #3fa9f6;
            border: 3px #3fa9f6 solid;
            .icon-lightbulb,
            .icon-exclamation {
                font-size: 24px !important;
                line-height: 45px;
            }
            .icon-handPointer {
                font-size: 26px !important;
                line-height: 40px;
            }
            &.complete {
                border-color: #8ccc41;
                color: #8ccc41;
            }
            &.inComplete {
                border-color: #3fa9f6;
                color: #3fa9f6;
            }
            .intractiveHandPointer {
                background: white;
                width: 11px;
                height: 17px;
                position: absolute;
                bottom: -6px;
                right: 4px;
                border-radius: 100%;
                .icon-intractiveHandPointer {
                    position: absolute;
                    bottom: 0px;
                    left: -2px;
                    font-size: 20px !important;
                }
            }
        }
        .tooltip {
            position: absolute;
            top: 55px;
            right: -65px;
            z-index: 1;
            text-align: right;
            .tooltip-inner {
                min-width: 150px;
                background-color: #fff;
                color: #fff;
                padding: 5px 10px;
                border-radius: 6px;
                position: relative;
                top: 0px;
                border: 1px #9d9d9d solid;
                box-shadow: 0px 4px 12px -3px rgba(50, 50, 50, 0.75);
                display: inline-block;
                text-align: center;
                word-wrap: break-word;
                line-height: 1.2em;
                .tooltipText {
                    color: #2F2D2D;
                }
                a {
                    margin: 0px;
                    padding: 0px;
                }
            }
        }
        .arrow_up {
            width: 0;
            height: 0;
            position: absolute;
            top: -12px;
            left: 0px;
            right: 0px;
            margin: 0 auto;
            display: inline-block;
            text-align: center;
            border-left: 13px solid transparent;
            border-right: 13px solid transparent;
            border-bottom: 12px solid #9d9d9d;
            .arrow_up-inner {
                width: 0;
                height: 0;
                position: relative;
                top: 1px;
                right: 12px;
                border-left: 12px solid transparent;
                border-right: 12px solid transparent;
                border-bottom: 12px solid #ffffff;
            }
        }
    }
}
