#wrapper{
  .modal {
    .content{
      border: none !important;
      background-color:lighten(@secondary-color,  30%) ;
      overflow: auto;
      .title{
        background-color: @navigation-color ;
        color: @font-color-inverted;
        padding: @item-padding (@item-padding * 4);
          .TOC-title h3{
          margin-bottom: 10px;
          }
        .articleTitle h4{
          font-weight: @font-weight-regular;
        }
      }
      .head .close{
        font-size:@icon-size;
        line-height: (@icon-size + 2);
        color: @item-text-color !important;
        border-color: #ffffff ;
        background-color: transparent;
        text-decoration: none;
        box-shadow: none;
        right: 20px;
        top: 15px;
      }
    }
  }

  .block-content-TOC-container{
    .block-thumbnail-container{
      background-color:#dee1e4 ;
      padding-right: 0px;
      padding-left: 35px;
      .toc-img-container{
        width:366px;
        height: 270px;
        background-color: #FFFFFF;
        border: 2px solid #102A3E;
        position:relative;
        padding: 0px 0px 40px;
        float:left;
        margin-right: 3px;
        margin-top: 1px;
       .block-thumbnail-img{
          cursor: pointer;
        }
        .blockContent-progress{
          position: absolute;
          right: 17px;
         .blockContent-progress-icon {
            outline: none;
            text-decoration: none;
            border-color: @primary-color;
            display: block;
            height: 13px;
            border-radius: 3px;
            color: @primary-color;
            height: 13px;
            width:13px;
            margin: 5px;
            &.visited {
              background-color: @secondary-color;
              color: @primary-color;
            }
            &.active {
              background-color: @primary-color;
              color: @primary-color;
            }
          }
        }
        .block-title-TOC{
          margin-left: 10px;
          font-weight: @font-weight-regular;
          color:@primary-color;
          font-size: (@icon-size - 8 ) !important;
          margin-top: 6px;
          padding: 15px 0px;
        }
      }
    }
  }

  .navigation {
    .navigation-TOC-button{
      position: relative;
      float: right;
      margin-top: 14px;
      margin-left: -20px;
      margin-right: 13px;
      cursor: pointer;
    }
    .page-level-progress-navigation-completion {
      margin:6px 13px 0px -20px !important;
    }
    .blockContentItems-inner{
      position: relative;
      display: block;
      float: right;
      margin-right: 10px;
      margin-top: 8px;
      &.settingForExpandNav{
        margin-top: -20px;
      }
      .page-level-progress-navigation & {
        padding: 15px 0px !important;
      }
      .navigation-block-content{
        position: relative;
        float: left;
        margin-left: 0px;
        
        .block-content-item {
          margin-top: 6px;
          margin-left: 10px;
        }
        .navigation-TOC-button-expandationNav{
          cursor: pointer;
        }
        .navigation-speaker-note-button{
          &.disabled{
              color: grey !important;
              cursor: default;
          }
          cursor: pointer;
        }
        
        .navigation-info-button{
          cursor: pointer;
        }
      }
      .reply-all {
        position: relative;
        float: right;
        margin-top:6px;
        margin-left: 5px;
        margin-right: 5px;
        cursor: pointer;
        padding-bottom: 10px;
      }
    }
  }

  .block-content-items-info{
    .block-content-items-info-inner{
      position: fixed;
      z-index: 100;
      transition: 1s all;
      -webkit-transition: 1s all;
      -moz-transition: 1s all;
      -ms-transition: 1s all;
      .logo {
        padding: 0px;
        float: left;
      }
      .module-name{
        float: right;
      }
      .block-content-items-info-title{
        color: #fff;
        text-align: center;
        padding-top: 15px;
        padding-right: 5px;
      }
      height: @bottomnavigation-height;
    } 
    .maximize{
      margin-top:-56px;
      width: 100%;
    }
    .minimize{
      margin-top:-104px;
      margin-left: 40px;
      width: 97%;
    }
    .min-info-show {
      margin-top:-64px;
    }
    .min-info-hidden{
      margin-top:-104px;
    }
    .max-info-show{
      margin-top:-12px;
    }

    .max-info-hidden{
      margin-top:-56px;
    }
  }
}
