.casInteractive-component {
    .casInteractive-inner {
        .row-bg {
            background: #000000;
            .fa-external-link-square {
                color: @item-text-color;
                cursor: pointer;
            }
        }
        table {
            width: 100%;
        }
        .column-1 {
            width: 20px;
            text-align: center;
        }
    }
}

.casInteractive-popup {
    position: absolute;
    display: inline-block;
    z-index: 11;
    table {
        width: 100%;
        max-width: 520px;
        max-height: 500px;
    }
    .casInteractive-popup-inner {
        position: relative;
        width: 600px;
        background-color: #ffffff;
        border: 2px solid #102A3E;
        border-radius: 10px;
        font-size: 16px;
        overflow: hidden;
        padding: 10px 10px;
        //margin: 20px auto;
        .popup-toolbar {
            min-height: 36px;
            color: #ffffff;
            cursor: move;
            .popup-toolbar-inner {
                position: relative;
                .popup-title {
                    padding: 8px 10px;
                    border: 2px #DCDDDE solid;
                    border-radius: 5px;
                    .popup-title-inner {
                        background: #000000;
                        display: inline-block;
                        padding: 2px 5px !important;
                        line-height: 28px;
                    }
                }
                button.popup-close {
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    padding: 8px 10px;
                    .icon-close {
                        cursor: pointer;
                        color: #ffffff;
                        float: right;
                        top: -5px;
                        right: -5px;
                        position: absolute;
                        background: #102A3E;
                        border-radius: 100%;
                        padding: 3px 5px;
                        font-size: 16px;
                    }
                }
            }
        }
        .popup-content {
            border: 2px #DCDDDE solid;
            padding: 10px 25px;
            border-radius: 5px;
            margin-top: 5px;
            .popupTable {
                margin-top: 10px;
            }
        }
    }
}

